body {
    background-color: rgb(21 24 29);
    color: #eee;
}

input :focus {
    outline: none;
    border: solid 0.2px;
}

.App {
    text-align: center;
    width: 100%;
}

.mainLogo {
    font-size: 70px;
    font-weight: bold;
    width: 700px;
    margin: 25px auto 50px;
}

.searchBarBox {
    margin: 0 auto;
    width: 700px;
    position: relative;
}
.searchBar {
    border: solid 0.2px;
    border-radius: 10px;
    border-color: #707f96;
    height: 35px;
    margin-right: 15px;
    width: 100%;
    background-color: #343a46;
    color: #eee;

    text-align: center;
}

.searchBotton {
    position: absolute;
    right: 2px;
    top: 4px;
    cursor: pointer;
    background-color: rgb(21 24 29);
    color: #eee;
    width: 50px;

    border: solid 1px;
    border-radius: 15px;
    border-color: #2b313a;
}
.chaBox {
    background-color: #1c1f26;

    width: 700px;
    margin: 30px auto;
    padding: 30px 0;
    box-shadow: 4px 6px 11px -5px #000000;

    border: solid 1px;
    border-radius: 12px;
    border-color: #2b313a;
}

.nameof {
    font-weight: bold;
    font-size: 2rem;
}

.chaImg {
    width: 300px;
    border: solid 1px;
    border-radius: 15px;
    border-color: #2b313a;
    margin-top: 5px;
}

.oneJNume {
    font-weight: bold;
}

.equBox {
    background-color: #1c1f26;

    margin: 50px 0;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    box-shadow: 4px 6px 11px -5px #000000;

    width: 700px;
    height: 300px;
    margin: 0 auto 1000px;
    justify-content: center;
    align-items: center;
    border: solid 1px;
    border-radius: 12px;
    border-color: #2b313a;
}

.itemBoxSmall {
    display: flex;
    flex-direction: row;
    height: 45px;
}

.itemBoxSmall > span,
.itemBoxSmall > img {
    display: block;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 45px;
    border: 1px;
    border-color: #2b313a;
}

@media all and (max-width: 768px) {
    body {
        font-size: 0.9rem;
    }
    .mainLogo {
        width: 470px;
    }
    .searchBarBox {
        width: 470px;
    }
    .chaBox {
        width: 470px;
    }

    .equBox {
        width: 470px;
    }
}
